:root {
    --main-color: rgb(32, 34, 37);
    --main-color-light: rgb(57, 60, 65);
    --main-color-extralight: rgb(47, 49, 54);
    --gray-light: #f7f7f7;
    --text: rgb(165, 170, 176);
    --text-light: #f6f6f6;
    --red: #bf5050;
    --green: #70b35f;
    --red-light: #c36e6e;
    --green-light: #9ac38f;

    --header-bg: var(--main-color-extralight);
    --header-text: var(--text);

    --app-bg: rgb(54, 57, 63);

    --navbar-text: var(--text);
    --navbar-icon: rgb(141, 145, 150);
    --navbar-icon2: rgb(246, 246, 250);
    --navbar-bg: var(--main-color);
    --navbar-exit: rgb(240, 71, 71);
    --navbar-exit-hover: rgb(238, 114, 114);
    --navbar-exit-bg: #413338;

    --block-bg: #2f3136;

    --btn-bg: rgb(113, 136, 212);
    --btn-hover: rgb(102, 122, 191);
    --btn-text: var(--text-light);
    --btn-nav-hover: var(--main-color-extralight);

    --footer-text: var(--main-color-light);
    --footer-bg: var(--main-color-extralight);

    --underline: rgb(96, 96, 96);
}

button {
    background: none;
}

.input {
    border-radius: 5px;
    border: 1px solid rgb(96, 96, 96);
    border: 1px solid var(--underline);
    padding: 0.7em;
}

.error-text {
    color: #bf5050;
    color: var(--red);
}

.error {
    border: 2px solid #bf5050;
    border: 2px solid var(--red);
}

.form {
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 0.3fr 1fr;
    grid-gap: 1em;
    background: rgb(47, 49, 54);
    background: var(--header-bg);
    padding: 1em;
    min-width: 450px;
    border-radius: 5px;
}


#app-wrapper {
    /*display: grid;*/
    /*grid-template-areas:*/
    /*'header"*/
    /*"container"*/
    /*"container";*/
    /*grid-template-columns: 1fr;*/
    /*grid-template-rows: 60px 1fr 60px;*/
    font-family: 'Open Sans', sans-serif;
    height: 100%;
}

.separator {
    margin: 8px 10px;
    height: 1px;
    background: #727272;
}

body {
    background: rgb(54, 57, 63);
    background: var(--app-bg);
}

.container {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    grid-area: container;
    padding: 1.5em;
    height: 100%;
    font-size: 17px;
    font-weight: 500;
    color: #f6f6f6;
    color: var(--text-light);
}

.label {
    padding-left: 0.7em;
}

.formTitle {
    margin: 0 auto;
    font-size: 20px;
}

.block {
    background-color: #2f3136;
    background-color: var(--block-bg);
    border-radius: 5px;
    padding: 1em; 
}

.btn {
    padding: .5em 1em;
    background: rgb(113, 136, 212);
    background: var(--btn-bg);
    transition: .3s;
    max-width: 300px;
    border-radius: 5px;
    width: 100%;
    color: #f6f6f6;
    color: var(--btn-text);
    font-weight: bold;
}

.btn:hover {
    background: rgb(102, 122, 191);
    background: var(--btn-hover);
}

.underline {
    max-width: 100%;
    width: 100%;
    height: 2px;
    border-radius: 5px;
    margin-top: 1em;
    background: rgb(96, 96, 96);
    background: var(--underline);
}
/*Обнуление*/
*{padding:0;margin:0;border:0;}
*,*:before,*:after{box-sizing:border-box;}
:focus,:active{outline:none;}
a:focus,a:active{outline:none;}
nav,footer,header,aside{display:block;}
html,body{height:100%;width:100%;font-size:100%;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%;}
input::-ms-clear{display:none;}
input,button,textarea{font-family: inherit;}
button{cursor:pointer;}
button::-moz-focus-inner{padding:0;border:0;}
a,a:visited{text-decoration:none;}
a:hover{text-decoration:none;}
ul li{list-style:none;}
img{vertical-align:top;}
h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:inherit;}
